import React from "react";
import "./Button.css";

function Button({ type, title, disable, onClick }) {
  return (
    <div
      className={`btn ${
        (type === "add" && "add") ||
        (type === "remove" && "remove")
      }`}
      disabled={disable}
      onClick={onClick}
    >
      {title}
    </div>
  );
}

export default Button;