const { getData } = require("./db/db");
const foods = getData();
const tele = window.Telegram.WebApp;
const token = "5468424043:AAFV7yfRA7EpyZ9VOTA75LRfnYcWVV-oUZw";
const axios = require('axios');

function App() {
  const [cartItems, setCartItems] = useState([]);
  const [totalPrice, setTotalPrice] = useState(0);
  const [initData, setInitData] = useState(tele.initDataUnsafe);

  const getPrices = () => {
    const res = [];
    cartItems.forEach((a, i) => {
      res.push({label: `${a.title} x${a.quantity}`, amount: a.price * a.quantity * 100})
    });
    return res;
  }

  useEffect(() => {
    tele.ready();
    tele.MainButton.onClick(() => {
      fetch('db.php')
      // {new_order:JSON.strinigify({
      //   user_id: initData.user.id,
      //   //name: initData.user.first_name,
      //   goods:JSON.strinigify(cartItems.map(a => a.title)),
      //   quant: JSON.strinigify(cartItems.map(a => a.quantity)),
      //   amount: totalPrice
      // })})
      // .then(res => {
      //   fetch(`https://api.telegram.org/bot${token}/sendInvoice`, {method:'POST', headers:{'Content-Type':'application/json'}, body:JSON.strinigify({
      //     chat_id: initData.user.id,
      //     title: `Замовлення №${res}`,
      //     description: 'Сплатіть за ваше замовлення тут',
      //     payload: {
      //       unique_id: `000${res}${Date.now()}`,
      //       provider_token: "632593626:TEST:sandbox_i40694328466"
      //     },
      //     provider_token: "632593626:TEST:sandbox_i40694328466",
      //     currency: "UAH",
      //     prices: getPrices()
      //   })})
      // })
      // .then(res => {
      //   tele.MainButton.text = res;
      // })
      .then(res => tele.MainButton.text = JSON.stringify(res))
      .catch(err => tele.MainButton.text = err)
    })
  });

  useEffect(() => {
    setTotalPrice(cartItems.reduce((a, c) => a + c.price * c.quantity, 0));
  }, [cartItems])

  const onAdd = (food) => {
    const exist = cartItems.find((x) => x.id === food.id);
    if (exist) {
      setCartItems(
        cartItems.map((x) =>
          x.id === food.id ? { ...exist, quantity: exist.quantity + 1 } : x
        )
      );
    } else {
      setCartItems([...cartItems, { ...food, quantity: 1 }]);
    }
    tele.MainButton.text = "Оплатити";
    tele.MainButton.button_color = "#00B900";
    tele.MainButton.show();
  };

  const onRemove = (food) => {
    const exist = cartItems.find((x) => x.id === food.id);
    if (exist.quantity === 1) {
      setCartItems(cartItems.filter((x) => x.id !== food.id));
    } else {
      setCartItems(
        cartItems.map((x) =>
          x.id === food.id ? { ...exist, quantity: exist.quantity - 1 } : x
        )
      );
    }
    if(cartItems.length === 1){
      tele.MainButton.hide();
    }
  };

  return (
    <>
      <div className="cart__container">
        <span className="">Total Price: {totalPrice.toFixed(2)}₴</span>
      </div>
      <div className="cards__container">
        {foods.map((food) => {
          return (
            <Card food={food} key={food.id} onAdd={onAdd} onRemove={onRemove} />
          );
        })}
      </div>
    </>
  );
}

export default App;
